.Form {
	color: #d4d4d4;
	background-color: #1e1e1e;
	padding: 0.5em;
	cursor: text;
}

.Form pre {
	margin: 0;
}

.Form-comment {
	color: #608b4e;
}

@media (min-width: 1200px) {
	.Form {
		font-size: 2em;
		border: 1px solid #3c3c3c;
		border-radius: 0.125em;
	}
}



